.header {
	z-index: 2;
	&__inner {}
	&__top {
		color: $color-white;
		background: $color-black--light;
		padding: 15px 0;
		.uk-flex {
			//+max-screen($smartphone-portrait)
 }			//lign-items: center
		.uk-button {
			transition: all .2s; }
		a {
			color: $color-white; } }
	&__bot {
		@include max-screen($big-tablet) {
			overflow: auto;
			-webkit-overflow-scrolling: touch; }
		> .uk-container {
			@include max-screen($big-tablet) {
				padding: 0 4px; } }
		.uk-navbar-nav {
			@include max-screen($big-tablet) {
				min-width: 940px; } }
		.uk-dropdown {
			@include max-screen($big-tablet) {
				position: fixed;
				top: 105px!important;
				//margin-top: 60px !important
 } } }				//left: auto!important

	&__left-nav,
	&__right-nav {
		font-size: 28px; }

	&__right-nav {
		margin-left: 14px; }

	&__logo {
		@include max-screen($big-tablet) {
			margin-left: 20px;
			max-width: 160px; }
		@include max-screen($smartphone-portrait) {
			margin-bottom: 4px;
			max-width: 120px; } }
	&__search {
		margin-left: 60px;
		@include max-screen($big-tablet) {
			margin-left: auto; } }
	&__ui {
		margin-left: auto;
		@include max-screen($mid-tablet) {
			display: none; }
		> * {
			&:nth-child(1n+2) {
				margin-left: 30px; } }
		.uk-icon-sort-down {
			margin-left: 6px;
			transform: translateY(-4px);
			//&:before
			//isplay: inline-block
 }			//argin-top: -4px
		.icon {
			transition: color .2s;
			&:hover, &:focus {
				color: $color-orange; } } }
	&__auth {
		@include max-screen($big-tablet) {
			display: none; }
		a {
			display: inline-block;
			font-size: 24px;
			transition: all .2s;
			&:nth-child(1n+2) {
				margin-left: 25px; }
			&:hover, &:focus {
				color: $color-yellow;
				text-decoration: none; } } } }

.search {
	position: relative;
	&__input {
		display: inline-block;
		@include max-screen($small-tablet) {
			position: absolute;
			display: block;
			width: 300px;
			top: 0;
			right: 25px;
			opacity: 0;
			visibility: hidden;
			transition: all .2s 0.15s;
			@include max-screen($smartphone-landscape - 1) {
				width: 180px; }
			input {
				width: 100%; }
			&.is-open {
				opacity: 1;
				visibility: visible; } } }
	&__btn {
		@include max-screen($small-tablet) {
			background: transparent;
			font-size: 23px;
			padding: 0;
			transition: all .4s;
			&.is-active {
				color: $color-orange!important; }
			&:hover, &:focus {
				background: inherit;
				color: inherit; } } } }
.profile {
	&__i-wrap {}
	&__info {
		margin-left: 10px; }
	&__name {}
	&__balance {
		font-size: 12px; } }
