.home {}

.main-slider {
	&__text {
		@include centre(both); }
	img {
		@include max-screen($desktop) {
			height: 300px!important; } } }

.about-services {
	//padding: 90px 0 140px
	//background: $color-bg-sky
	&__intro {
		margin-bottom: 50px; }
	&__grid {
		margin-bottom: 100px; }
	&__pager {} }

.about-card {
	position: relative;
	border: 1px solid $color-border-gray;
	@include max-screen($smartphone-landscape) {
		max-width: 225px;
		margin: 0 auto; }
	&__head {
		position: relative;
		display: block;
		overflow: hidden;
		&:hover .about-card__author {
			transform: translateY(-100%); } }
	&__img-w {
		img {
			width: 100%; } }
	&__badge {
		position: absolute;
		top: -4px;
		left: 10px; }
	&__author {
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;

		background: rgba(50, 50, 50, 0.5);
		color: $color-white;

		padding: 4px 8px;

		transition: all .3s ease-out; }
	&__body {
		padding: 10px; }
	&__text {
		font-size: 14px; }
	&__footer {
		margin-top: 30px; }
	&__actions {
		a {
			font-size: 17px;
			transition: color .2s;
			&:hover, &:focus {
				color: $color-orange; }
			&:nth-child(1n+2) {
				margin-left: 6px; } } }
	&__price {
		margin-left: auto;
		font-size: 16px;
		font-weight: 500;
		.nums {
			font-size: 24px;
			color: $color-orange;
			font-weight: 700; } } }

.ac-author {
	display: flex;
	&__info {
		img {
			margin-right: 10px; } }
	&__review {
		margin-left: auto; }
	&__rating {}
	&__stars {
		color: $color-yellow--light;
		i {
			&:nth-child(1n+2) {
				margin-left: 2px; } } }
	&__num {
		margin-left: 5px; } }

//section
//130
.reviews {
	&__hline {
		position: relative;
		&:before, &:after {
			@include centre(y);
			display: inline-block;
			content: "";
			height: 10px;
			background: $color-border-gray; }
		&:before {
			left: 0;
			right: calc(50% - (-170px)); }
		&:after {
			right: 0;
			left: calc(50% + 170px); }
		.clr-red {
			vertical-align: text-top; } }
	&__arrow {
		@include centre(y);
		font-size: 112px;
		transition: color .2s;
		@include max-screen($common-tablet) {
			font-size: 60px;
			top: 24%; }
		@include max-screen($smartphone-landscape - 1) {
			top: 35%; }
		&--left {
			left: 30px;
			@include max-screen($common-tablet) {
				left: 0; } }
		&--right {
			right: 30px;
			@include max-screen($common-tablet) {
				right: 0; } } } }
.review-card {
	&__body {}
	&__img-w {
		//width: 200px
		@include max-screen($common-tablet) {
			margin-top: 60px;
			margin-bottom: 20px; } }
	&__info {
		padding-top: 12px; }
	&__head {}
	&__rating {
		display: inline-block;
		color: $color-yellow--light;
		i {
			&:nth-child(1n+2) {
				margin-left: 3px; } }
		@include max-screen($smartphone-portrait) {
			margin-top: 10px; } }
	&__main {}
	&__text {}
	&__more {} }
