// override if need  usage: +container(maw, gutter)
$container-lg: 1280px;
$container-md: 800px;
$container-sm: 580px;
$container-gutter: 20px;

$section-offset: 100px;
$section-sm-offset: 60px;

.wrap {
 }	//overflow: hidden

.headline-yellow {
	display: inline-block;
	//font-size: 20px
	padding-top: 10px;
	border-top: 2px solid $color-yellow; }

.container {
	margin-left: auto;
	margin-right: auto;
	@include clearfix;
	&--lg {
		max-width: $container-lg;
		padding-left: $container-gutter;
		padding-right: $container-gutter;
		position: relative; }
	&--md {
		max-width: $container-md; }
	&--sm {
		max-width: $container-sm; } }

.gutter {
	padding-left: $container-gutter;
	padding-right: $container-gutter; }

.section + .section {
	&:nth-child(1n+2) {
		margin-top: $section-offset; } }

.section-sm + .section-sm {
	&:nth-child(1n+2) {
		margin-top: $section-sm-offset; } }


//default grids
.u-grid-1-2 {
	@include make-grid(50%, 70px);
	@include max-screen($big-tablet) {
		@include break-grid(100%, 0, 40px); } }
.u-grid-1-3 {
	@include make-grid(33.33333%, 90px);
	@include max-screen($big-tablet) {
		@include break-grid(100%, 0, 40px); } }
.u-grid-1-5 {
	@include make-grid(20%, 70px);
	@include max-screen($big-tablet) {
		//!!!!
		@include break-grid(33.3333%, 40px, 60px);
		justify-content: center; }
	@include max-screen($small-tablet) {
		@include break-grid(50%, 30px, 40px); }
	@include max-screen($smartphone-portrait) {
		@include break-grid(100%, 0px, 40px); } }


// generic tea
$tea-gutter: 20px;
$tea-top: 40px;

[class*="u-grid-1-"] {
	display: flex;
	flex-flow: row wrap;
	margin-left: -$tea-gutter;
	> * {
		padding-left: $tea-gutter;
		float: left;
		margin-top: $tea-top; } }

.u-grid-1-2 {
	> * {
		width: 50%; } }
.u-grid-1-3 {
	> * {
		width: 33.33333%; } }
.u-grid-1-4 {
	> * {
		width: 25%; } }
.u-grid-1-5 {
	> * {
		width: 20%; } }

// responsive
.u-grid-break-lg {
	@include max-screen($big-tablet) {
		margin: 0;
		> * {
			width: 100%;
			padding-left: 0; } } }
.u-grid-break-md {
	@include max-screen($mid-tablet) {
		margin: 0;
		> * {
			width: 100%;
			padding-left: 0; } } }
.u-grid-break-sm {
	@include max-screen($small-tablet) {
		margin: 0;
		> * {
			width: 100%;
			padding-left: 0; } } }
